.vetskitchen-how-it-works {
  background-color: #f2f4f5;
}
.vetskitchen-how-it-works__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 50px 0 60px;
  grid-gap: 30px;
  background-image: url("../../assets/images/how-it-works-banner.webp");
  background-size: cover;
}
.vetskitchen-how-it-works .heading {
  color: #018077;
}
.vetskitchen-how-it-works .steps {
  counter-reset: steps;
}
.vetskitchen-how-it-works .steps__item {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
  font-size: 16px;
  font-weight: 500;
  color: #000000;
}
.vetskitchen-how-it-works .steps__item .counter {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 64px;
  height: 64px;
  font-family: "Signika", sans-serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 1;
  color: #ffffff;
  background-color: #2ea5ea;
  border-radius: 50%;
}
.vetskitchen-how-it-works .steps__item .counter:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  border: 1px dashed #ffffff;
  border-radius: 50%;
}
.vetskitchen-how-it-works .steps__item .text {
  max-width: 360px;
}
.vetskitchen-how-it-works .steps__item .text > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-how-it-works .steps__item .text p {
  margin-bottom: 5px;
}
.vetskitchen-how-it-works .left-content {
  text-align: center;
  background-image: url("../../assets/images/how-it-works-banner.webp");
  background-size: cover;
}
.vetskitchen-how-it-works .left-content__image {
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}
.vetskitchen-how-it-works .right-content__bottom {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  grid-gap: 20px;
  padding-top: 100px;
}
.vetskitchen-how-it-works .right-content__bottom .text {
  position: relative;
  padding-bottom: 14px;
  font-family: "Signika", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #353535;
}
.vetskitchen-how-it-works .right-content__bottom .text:after {
  content: "";
  position: absolute;
  left: 2px;
  bottom: -5px;
  width: 100px;
  height: 10px;
  background: url("../../assets/images/underline.webp") no-repeat;
  background-size: contain;
}
@media screen and (max-width: 991px) {
  .vetskitchen-how-it-works .right-content__bottom {
    flex-direction: column;
    justify-content: center;
    padding-top: 40px;
  }
}
@media screen and (max-width: 767px) {
  .vetskitchen-how-it-works__wrapper {
    grid-template-columns: 1fr;
    padding-top: 0;
    background-image: none;
  }
  .vetskitchen-how-it-works .heading {
    margin-bottom: 0;
    font-size: 23px;
  }
  .vetskitchen-how-it-works .left-content {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    margin-right: -12px;
    margin-left: -12px;
    background-color: #ffffff;
  }
  .vetskitchen-how-it-works .left-content__image {
    margin: 0;
  }
  .vetskitchen-how-it-works .right-content {
    padding: 0 18px;
  }
}